<template>
    <div>
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <div v-html="dataHtml" class="dataimg"></div>
        <div class="recommend">
            <h4>专题推荐</h4>
            <div class="recommend-list">
                <div class="recommend-item" v-for="item in data" :key="item.id">
                    <img :src="item.scene_pic_url" alt="">
                    <p>{{item.title}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        detailaction
    } from "@/api/topic/detailaction/index.js"
    export default {
        data() {
            return {
                dataHtml: '',
                data: [],
            };
        },
        computed: {},
        created() {


        },
        mounted() {

            detailaction({
                id: this.$route.query.id
            }).then(res => {
                this.dataHtml = res.data.data.content;
                this.data = res.data.recommendList
            })

        },
        methods: {
            onClickLeft() {
                history.back()
            },
        },
    };
</script>
<style lang="scss">
    .dataimg {
        img {

            width: 100%;
        }
    }
</style>
<style scoped lang="scss">

    .recommend {
        h4 {
            width: 100%;
            text-align: center;
            height: 50px;
            line-height: 50px;
        }

        .recommend-list {
            padding: 0px 15px;

            .recommend-item {
                padding: 10px;
                background-color: #fff;
                margin-bottom: 10px;

                img {
                    display: block;
                    margin: 0 auto;
                    width: 100%;
                    height: 139px;
                }

                p {
                    margin: 20px 0px;
                    font-size: 14px;
                }
            }
        }
    }
</style>